<template>
	<div class="tab_home">
    <van-tabs v-model="activeIndex" @click="handleTabClick">
			<van-tab v-for="tab in tabsItem" :title="tab.name" :key="tab.type"></van-tab>
		</van-tabs>

		<van-list v-model="loading" :finished="finished" :immediate-check="false" @load="loadMore">
					<van-card
						v-for="(goods, index) in items"
						class="order_list--van-card"
						:key="index"
						:thumb="goods.goods_img">
            <div slot="title" class="van-card__row">
              <div class="van-card__title">{{ goods.goods_key }}</div>
              <div v-if="activeIndex!=2" class="van-card__price" style="color: red;">
                {{STATUS_TEXT[goods.task_status]}}
              </div>
              <div v-if="activeIndex == 2" class="van-card__price" style="color: red;">
                  {{comStatusDes[goods.commentStatus]}}
              </div>
            </div>

            <div slot="desc" class="van-card__row">
              <div class="van-card__desc">{{'佣金 ￥' + goods.brokerage}}</div>
              <!-- <div v-if="activeIndex!=2" class="van-card__num" style="color: darkred;">{{goods.endTime+' 过期'}}</div> -->
            </div>

						<div v-if="goods.task_status == 1 && goods.order_type == 1" slot="desc" class="van-card__row wait_money">
               <div class="van-card__desc" style="color: darkred; font-size: 14px;">等待商家打款</div>
            </div>

            <div v-if="goods.order_type == 2 && goods.task_status == 3 && goods.delivery == 0" style="color:red;" slot="desc" class="van-card__row">
                (务必等到签收后再确认收货若没物流信息，3-5天之后确认收货）
            </div>

            <div v-if="goods.task_status == 3 && goods.comment_type == 3" slot="desc" class="van-card__row wait_money">
               <div class="van-card__desc" style="color: red; font-size: 14px;">请按照要求进行评价</div>
            </div>

            <component
              class="order_list--footer_btn"
              slot="footer"
              :is="actionBtn"
              :status="goods.task_status"
              :payStatus="goods.payStatus"
              :orderType="goods.order_type"
              :delivery="goods.delivery"
              :commentStatus="goods.commentStatus"
              :tabIndex="activeIndex"
              :payLoading="payLoading"
              @handle="actionHandle($event, index)"
            />

          </van-card>
		</van-list>

		<is-empty v-if="isEmpty">暂无数据</is-empty>

	</div>
</template>

<script>
import { SALES_LIST } from "@/api/task";
import { VERIFY_ORDER } from "@/api/user";
import { VERIFY_DELIVERY } from "@/api/task";
import { Tab, Tabs, Panel, Card, Button, Toast, Dialog } from "vant";
import IsEmpty from "@/vue/components/is-empty/";
import listBtn from "./list-btn";

import loadMore from "@/vue/mixin/list-load-more";

const STATUS_TEXT = {
  0: "待拍下",
  1: "等待商家返款", //已拍下
  2: "商家已打款",
  3: "已完成",
  4: "已取消",
  5: "已超时"
};

const COMMENT_TEXT = {
  0: "待好评",
  1: "待商家核对",
  2: "已核对"
};

export default {
  name: "orders",
  mixins: [loadMore],
  data() {
    return {
      activeIndex: 0,
      actionBtn: "listBtn",
      STATUS_TEXT: STATUS_TEXT,
      comStatusDes: COMMENT_TEXT,
      isEmpty: false,
      payLoading: false,
      tabsItem: [
        {
          name: "待付款",
          index: 0
        },
        {
          name: "待收货",
          index: 1
        },
        {
          name: "待好评",
          index: 2
        },
      ]
    };
  },
  watch: {
    $route: "changeRouter"
  },
  created() {
    this.initData();
  },

  methods: {
    changeRouter() {
      this.$router.go(0);
    },
    initData() {
      let source = this.$route.params.source;
      if (source == 1) {
        //如果是评论提交跳转过来 切换到待好评列表
        this.activeIndex = 2;
        this.$route.params.source = "";
      }
      return this.$reqGet(
        SALES_LIST,
        {
          per_page: this.pages.perPage,
          page: this.pages.currPage,
          tab_index: this.activeIndex
        },
        {
          hideLoading: true
        }
      ).then(res => {
          console.log(res);
          return this.loadSuccess(res);
      })
    },
    loadSuccess(res) {
      if(res.data.data == null || !res.data.data.list){
          setTimeout(() => {
            this.isEmpty = true;
          }, 600);
          return this.pages;
      }
      let list = res.data.data.list;
      let page = {
        pageCount: res.data.data.pageCount,
        currPage: res.data.data.currPage,
        perPage: res.data.data.perPage,
        totalCount: res.data.data.totalCount
      };
      this.items.push(...list);
      return page;
    },
    toDetails(i) {
      let id = this.items[i].order_id;
      let status = this.items[i].task_status;
      let collect_status = this.items[i].task_status;
      if (status == 0) {
        this.$router.push({ name: "salesDetails", params: { id: id } });
      } else {
        this.$router.push({ name: "viewDetails", params: { id: id } });
      }
      // if(collect_status == 0 || collect_status == 3){ // 隔日单未审核
      //   this.$router.push({ name: "collectUpload", params: { id: id } });
      // }else{
      //   if (status == 0) {
      //     this.$router.push({ name: "salesDetails", params: { id: id } });
      //   } else {
      //     this.$router.push({ name: "viewDetails", params: { id: id } });
      //   }
      // }
    },
    //确认付款
    toPay(i) {
      let _money = this.items[i].brokerage;
      let orderType = this.items[i].order_type;
      if (orderType == 1) {
        //鱼
        var tipStr =
          `<p style="text-align: center;">恭喜完成任务，获得` +
          _money +
          `元奖励，您可以前往"个人中心"进行提现。</p>
						<p style="text-align: center; color: darkred;">(请不要提前确认收货，不要提前确认收货；务必等到宝贝签收之后再点确认收货~!)</p>
						<p style="text-align: center; color: darkred;">（确认收货之后，请给全5星好评，并且自由发挥评价，么么哒）</p>`;
      } else if (orderType == 2) {
        //邀请单
        var tipStr =
          `<p style="text-align: center;">任务付款成功~！</p>
						<p style="text-align: center; color: darkred;">(请务必签收之后再确认收货。)</p>
            <p style="text-align: center; color: darkred;">（如果没有物流信息，则3-5天之后再确认收货）</p>
            <p style="text-align: center; color: darkred;">本金次日之后返还</p>
            <p style="text-align: center; color: darkred;">您好评之后，即可获得佣金${_money}元</p>
            `;
      }
      this.payLoading = true
      this.$reqPost(VERIFY_ORDER, {
        order_id: this.items[i].order_id
      }).then(res => {
        Toast.clear();
        this.payLoading = false
        if (res.data.code == 0) {
          Dialog.alert({
            message: tipStr
          }).then(() => {
            if(orderType == 2){
              this.handleTabClick(1);
            }else{
              this.$router.go(0);
            }
          });
        }
      }).catch(err => {
         this.payLoading = false
      });
    },
    //确认收货
    toDelivery(i) {
      Dialog.confirm({
        message: "确定要执行收货操作？"
      })
        .then(() => {
          Toast.loading({
            mask: true,
            message: "校验中..."
          });
          this.$reqPost(VERIFY_DELIVERY, {
            order_id: this.items[i].order_id
          })
            .then(res => {
              Toast.clear();
              Dialog.alert({
                message: res.data.msg
              }).then(() => {
              });
              // 页面刷新
              if (res.data.code == 0) {
                this.handleTabClick(2);
              }
            })
            .catch(err => {
              Toast.clear();
              Toast(err.data.msg);
            });
        })
        .catch(() => {});
    },
    //去好评
    toComment(i) {
      let id = this.items[i].order_id;
      this.$router.push({ name: "comment", params: { id: id } });
    },
    handleTabClick(i) {
      let active = this.tabsItem[i].index;
      if (this.activeIndex != active) {
        this.activeIndex = active;
        this.resetInit();
      }
    },
    getStatusText(status) {
      return STATUS_TEXT[status] || "";
    },
    actionHandle(handle, i) {
      this[handle] && this[handle](i);
    }
  },
  components: {
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    [Panel.name]: Panel,
    [Card.name]: Card,
    [IsEmpty.name]: IsEmpty,
    [Button.name]: Button,
    listBtn,
    [Toast.name]: Toast
  }
};
</script>

<style lang="scss" scoped>
.order_list {
  background-color: #f9f9f9;
  padding-bottom: 0;
  &--footer_btn {
    text-align: right;
  }
  &--panel {
    margin-bottom: 10px;
  }

  &--van-card {
    background-color: #ffffff;
    height: 120px;
  }

  &--total {
    text-align: right;
    padding: 10px;
  }
}
.wait_money {
  margin: 2px 0;
  flex-direction: row-reverse;
}
</style>



