<template>
	<div slot="footer">
    <van-button v-if="status == 0" size="small" type="danger" @click="$emit('handle', 'toDetails')">开始任务</van-button>

    <!--鱼池订单情况 begin-->
    <van-button v-if="orderType == 1 && status ==2" size="small" :loading="payLoading" type="danger"
                @click="$emit('handle', 'toPay')">确认付款
    </van-button>
    <!--鱼池订单情况 end-->

    <!--邀请池订单情况 begin-->
    <van-button v-if="orderType == 2 && payStatus == 0 && status == 1" size="small" :loading="payLoading" type="danger"
                @click="$emit('handle', 'toPay')">确认付款
    </van-button>
    <van-button v-if="orderType == 2 && payStatus == 1 && delivery == 0 && status != 4" size="small" type="danger"
                @click="$emit('handle', 'toDelivery')">确认收货
    </van-button>
    <!--邀请池订单情况 end-->

    <van-button v-if="tabIndex == 2 && commentStatus == 0" size="small" type="danger"
                @click="$emit('handle', 'toComment')">去好评
    </van-button>

    <van-button v-if="status !=0 && commentStatus == 0" size="small" type="danger"
                @click="$emit('handle', 'toDetails')">查看详情
    </van-button>

    <van-button v-if="commentStatus == 1 || commentStatus == 2" size="small" type="danger"
                @click="$emit('handle', 'toComment')">查看详情
    </van-button>
  </div>
</template>

<script>
export default {
  props:['status','orderType','delivery','tabIndex','commentStatus','payStatus','payLoading']
};
</script>



